body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.preview {
    margin-left: 20px;
}

.preview-button {
    background: #3176fb;
    color: white;
    padding: 10px;
    text-transform: uppercase;
    border: 0 none;
    border-radius: 5px;
    cursor: pointer;
}

.preview-text-input {
    padding: 10px;
    margin-right: 10px;
}

.preview-instructions {
    margin-top: 10px;
}
.List,
.Header {
  border: 1px solid #d9dddd;
  margin: auto;
}

.Header {
  white-space: nowrap;
}

.List {
  background: #ededed;
}

.List div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.even {
  background-color: #f8f8f0;
}

.odd {
  background-color: #ffffff;
}

.List div.long {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: clip;
  text-align: left;
  -webkit-justify-content: left;
          justify-content: left;
}

.List div.medium {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: clip;
}

.List div.link {
  text-decoration: underline;
  color: #3176fb;
  cursor: pointer;
}
.errors-grid {
  margin: 30px 0;
}

.index-batch-chart-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.App {
  text-align: center;
}

.App-link, .App a {
  color: #3176fb;
}

.App-navigation {
  list-style: none;
    margin-right: 30px;
}

.App-navigation li {
  display: inline-block;
  margin-right: 10px;
}

.clickable {
  cursor: pointer;
}

.link {
  text-decoration: underline;
  color: #3176fb;
  cursor: pointer;
}

.align-left {
  text-align: left;
}
